<template>
	<div class="b-products-slider__item js-products-slider-item" v-if="product">
		<div :class="['b-product-preview b-ib-wrapper', {'b-product-preview_actvie': product.BASKET_QUANTITY}]">
			
			<div class="b-product__count b-ib"><span class="b-count__in">{{product.BASKET_QUANTITY}} шт. в корзине</span></div>
			
			<!--<div class="b-product__star active">
				<a href="javascript:" class="prod__star"></a>
			</div>-->
			
			<div class="b-product-preview__pic b-ib" v-if="product.PRODUCT_PICTURE">
				<a :href="'#/catalog/' + product.PRODUCT_ID">
					<img :src="product.PRODUCT_PICTURE.src" />
				</a>
			</div>
			
			<div class="b-product-preview__name b-ib">
				<a :href="'#/catalog/' + product.PRODUCT_ID">
					{{product.PRODUCT_NAME}}
				</a>
			</div>
			<div class="b-product-preview__price b-ib">{{product.PRICE_FORMAT}} <span class="b-rouble">&#8381;</span></div>
			<div class="b-product-preview__buy b-ib">
				<form class="index_products_basket" @submit.prevent="">
					<!--<div class="b-product-preview__count b-ib">
						<input @input="watchQuantity" type="text" class="quantity_input b-product-preview__input" v-model="quantity" />
						<button  type="button" class="b-button b-button_plus" @click="plus">+</button>
						<button  type="button" class="b-button b-button_minus" @click="minus">–</button>
					</div>-->
					<div class="b-product-preview__incart b-ib">
						<button type="button" class="b-button b-button_green add_basket_btn" @click="addToBasket">В корзину</button>
					</div>
				</form>
				
			</div>
		</div>
	</div>
</template>
<script>

	export default {
		props: {
			product: {type: Object, required: true}
		},
		data() {
			return {
				quantity: 1,
			}
		},
		beforeUpdate() {
		},
		created() {
			if(this.product.BASKET_QUANTITY){
				this.quantity = _.toInteger(this.product.BASKET_QUANTITY);
			}
		},
		methods: {
			plus(){
				this.quantity++;
			},
			minus(){
				if(this.quantity > 1){
					this.quantity--;
				}
			},
			addToBasket(){
				window.MigBus.$emit('addToBasket', {
					quantity: this.quantity,
					product: this.product,
					sku: this.product.ID
				});
			},
			watchQuantity(ev){
				let val = _.toInteger(ev.target.value);
				
				if(ev.target.value.length === 0){
					val = '';
				} else if(val == 0){
					val = 1;
				}
				
				if(val > 0){
					this.quantity = val;
				}
			},
		},
		components: {},
		computed: {}
	}

</script>



